import React, { Component } from "react";
import ImageCard from "components/util-components/ImageCard";
import FlexibleContent from "./FlexibleContent";
import {
  AppstoreOutlined,
  BarsOutlined,
  CheckOutlined,
  CloseOutlined,
} from "@ant-design/icons";
import Flex from "components/shared-components/Flex";
import {
  Tag,
  Space,
  Button,
  Image,
  Modal,
  Layout,
  Typography,
  Row,
  Col,
  List,
  Avatar,
} from "antd";
import axios from "axios";

import AntImage from "components/util-components/AntImage";

const { Header, Footer, Sider, Content } = Layout;
const { Text } = Typography;

export class CardComponent extends Component {
  state = {
    imgCol: 4,
    lists: [],
    selectedList: {},
    viewStyle: 1, // 1: block view, 2: list view
    isModalOpen: false,
    selectedIndex: 0,
  };

  componentDidMount() {
    axios.get("http://localhost:5000/api/management/allSelfies").then((res) => {
      console.log("responddddddddddd:", res);
      this.setState({
        lists: [...res.data.allSelfie],
      });
    });
  }

  setImgCol = (col) => {
    this.setState({ imgCol: col });
  };
  showModal = (indiList, index) => {
    this.setState({
      isModalOpen: true,
      selectedList: indiList,
      selectedIndex: index,
    });
    console.log("indiList", indiList);
    console.log("selectedList", this.state.selectedList);
  };
  handleOk = () => {
    this.setState({ isModalOpen: false });
  };
  handleCancel = () => {
    this.setState({ isModalOpen: false });
  };

  handleStatusChange = (indiList, newStatus, index) => {
    const newIndiList = { ...indiList, status: newStatus };
    axios
      .post(
        "http://localhost:5000/api/management/changeSelfieStatus",
        newIndiList
      )
      .then((res) => {
        const newList = [...this.state.lists];
        this.setState({
          lists: [
            ...newList.slice(0, index),
            { ...newList[index], status: res.data.status },
            ...newList.slice(index + 1),
          ],
        });
      });
  };

  render() {
    return (
      <>
        <ImageCard>
          <Flex style={{ fontSize: "24px", marginBottom: "30px", gap: "15px" }}>
            <AppstoreOutlined
              onClick={() => {
                this.setState({ viewStyle: 1 });
              }}
            />
            <BarsOutlined
              onClick={() => {
                this.setState({ viewStyle: 2 });
              }}
            />
            <Button onClick={() => this.setImgCol(12)}>2</Button>
            <Button onClick={() => this.setImgCol(8)}>3</Button>
            <Button onClick={() => this.setImgCol(6)}>4</Button>
            <Button onClick={() => this.setImgCol(4)}>6</Button>
          </Flex>
          {this.state.viewStyle === 1 ? (
            <Row gutter={[16, 16]} justify="space-evenly">
              {this.state.lists.map((indiList, i) => (
                <Col span={this.state.imgCol} key={i}>
                  <FlexibleContent
                    showModal={() => this.showModal(indiList, i)}
                    onStatusChange={this.handleStatusChange}
                    indiList={indiList}
                    index={i}
                  />
                </Col>
              ))}
            </Row>
          ) : (
            <List
              itemLayout="horizontal"
              dataSource={this.state.lists}
              renderItem={(indiList, i) => (
                <List.Item
                  actions={[
                    <CheckOutlined
                      key="approve"
                      style={{ color: "#52c41a" }}
                      onClick={() =>
                        this.handleStatusChange(indiList, "approved", i)
                      }
                    />,
                    <CloseOutlined
                      key="block"
                      style={{ color: "#eb2f96" }}
                      onClick={() =>
                        this.handleStatusChange(indiList, "rejected", i)
                      }
                    />,
                    <Button
                      type="link"
                      onClick={() => this.showModal(indiList, i)}
                    >
                      View Details
                    </Button>,
                  ]}
                >
                  <AntImage alt="example" src={indiList.url} width="160px" />
                  <List.Item.Meta
                    avatar={<Avatar src={indiList.url} />}
                    title={<a href="javascript:void(0);">Face, half body</a>}
                    description="Artem Panchina"
                    style={{ marginBottom: "10px", marginLeft: "20px" }}
                  />
                  <Tag
                    color={
                      indiList.status === "approved"
                        ? "cyan"
                        : indiList.status === "rejected"
                        ? "red"
                        : "orange"
                    }
                  >
                    {indiList.status}
                  </Tag>
                </List.Item>
              )}
            />
          )}
          <Modal
            open={this.state.isModalOpen}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
            centered
            width="40%"
          >
            <Layout style={{ padding: "20px" }}>
              <Sider width="50%">
                <Image
                  alt="example"
                  src={this.state.selectedList.url}
                  preview={false}
                />
              </Sider>
              <Layout>
                <Header style={{ padding: "0 20px" }}>
                  <p style={{ fontSize: "28px", marginBottom: "0px" }}>
                    {this.state.selectedList.title}
                  </p>
                  <p
                    style={{
                      fontSize: "20px",
                      marginBottom: "0px",
                      color: "#287EF3",
                    }}
                  >
                    ${this.state.selectedList.price}
                  </p>
                </Header>
                <Content style={{ padding: "20px" }}>
                  <Space direction="vertical">
                    <Typography
                      style={{ fontSize: "28px", marginBottom: "0px" }}
                    >
                      Description
                    </Typography>
                    <Typography
                      style={{
                        fontSize: "18px",
                        marginBottom: "0px",
                      }}
                    >
                      {this.state.selectedList.description}
                    </Typography>
                    <Tag
                      color={
                        this.state.selectedList.status === "approved"
                          ? "cyan"
                          : this.state.selectedList.status === "rejected"
                          ? "red"
                          : "orange"
                      }
                    >
                      {this.state.selectedList.status}
                    </Tag>
                  </Space>
                  <Row gutter={10} style={{ marginTop: "20px" }}>
                    <Col>
                      <Button
                        large
                        type="primary"
                        onClick={() => {
                          this.handleStatusChange(
                            this.state.selectedList,
                            "approved",
                            this.state.selectedIndex
                          );
                          this.handleCancel();
                        }}
                      >
                        Approve
                      </Button>
                    </Col>
                    <Col>
                      <Button
                        large
                        type="primary"
                        danger
                        onClick={() => {
                          this.handleStatusChange(
                            this.state.selectedList,
                            "rejected",
                            this.state.selectedIndex
                          );
                          this.handleCancel();
                        }}
                      >
                        Reject
                      </Button>
                    </Col>
                  </Row>
                </Content>
                <Footer></Footer>
              </Layout>
            </Layout>
          </Modal>
        </ImageCard>
      </>
    );
  }
}

export default CardComponent;
