import React, { Component } from "react";
import {
  EditOutlined,
  EllipsisOutlined,
  SettingOutlined,
  CheckOutlined,
  CloseOutlined,
} from "@ant-design/icons";
import { Space, Card, Avatar, Tag } from "antd";

import AntImage from "components/util-components/AntImage";

const { Meta } = Card;

export class FlexibleContent extends Component {
  render() {
    return (
      <Card
        hoverable
        cover={<AntImage alt="example" src={this.props.indiList.url} />}
        actions={[
          <CheckOutlined
            key="approve"
            style={{ color: "#52c41a" }}
            onClick={() =>
              this.props.onStatusChange(
                this.props.indiList,
                "approved",
                this.props.index
              )
            }
          />,
          <CloseOutlined
            key="block"
            style={{ color: "#eb2f96" }}
            onClick={() =>
              this.props.onStatusChange(
                this.props.indiList,
                "rejected",
                this.props.index
              )
            }
          />,
          <SettingOutlined key="setting" onClick={this.props.showModal} />,
        ]}
      >
        <Meta
          avatar={
            <Space direction="vertical" align="left">
              <Avatar src={this.props.indiList.userAvatar} />
              <Tag
                color={
                  this.props.indiList.status === "approved"
                    ? "cyan"
                    : this.props.indiList.status === "rejected"
                    ? "red"
                    : "orange"
                }
              >
                {this.props.indiList.status}
              </Tag>
            </Space>
          }
          title={this.props.indiList.description}
          description={this.props.indiList.owner}
        />
      </Card>
    );
  }
}

export default FlexibleContent;
